/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'
import farmsConfig from 'config/constants/farms'
import fetchFarms from './fetchFarms'
import {
  fetchFarmUserEarnings,
  fetchFarmUserAllowances,
  fetchFarmUserTokenBalances,
  fetchFarmUserStakedBalances,
} from './fetchFarmUser'
import { FarmsState, Farm } from '../types'

const initialState: FarmsState = { data: { ...farmsConfig } }

export const farmsSlice = createSlice({
  name: 'Farms',
  initialState,
  reducers: {
    setFarmsPublicData: (state, action) => {
      const { farms, chainId }: { farms: Farm[]; chainId: number } = action.payload
      state.data[chainId] = state.data[chainId].map((farm) => {
        const liveFarmData = farms.find((f) => f.pid === farm.pid)
        return { ...farm, ...liveFarmData }
      })
    },
    setFarmUserData: (state, action) => {
      const { arrayOfUserDataObjects, chainId } = action.payload
      arrayOfUserDataObjects.forEach((userDataEl) => {
        const { index } = userDataEl
        state.data[chainId][index] = { ...state.data[chainId][index], userData: userDataEl }
      })
    },
  },
})

// Actions
export const { setFarmsPublicData, setFarmUserData } = farmsSlice.actions

// Thunks
export const fetchFarmsPublicDataAsync = (chainId: number) => async (dispatch) => {
  const farms = await fetchFarms(chainId)
  dispatch(setFarmsPublicData({ chainId, farms }))
}
export const fetchFarmUserDataAsync = (chainId, account) => async (dispatch) => {
  const userFarmAllowances = await fetchFarmUserAllowances(chainId, account)
  const userFarmTokenBalances = await fetchFarmUserTokenBalances(chainId, account)
  const userStakedBalances = await fetchFarmUserStakedBalances(chainId, account)
  const userFarmEarnings = await fetchFarmUserEarnings(chainId, account)

  const arrayOfUserDataObjects = userFarmAllowances.map((_, index) => ({
    index,
    allowance: userFarmAllowances[index],
    tokenBalance: userFarmTokenBalances[index],
    stakedBalance: userStakedBalances[index],
    earnings: userFarmEarnings[index],
  }))

  dispatch(setFarmUserData({ chainId, arrayOfUserDataObjects }))
}

export default farmsSlice.reducer
