export enum QuoteToken {
  'BNB' = 'BNB',
  'C100' = 'C100',
  'A009' = 'A009',
  'G009' = 'G009',
  'A010' = 'A010',
  'BUSD' = 'BUSD',
  'CAKE' = 'CAKE',
  'SLIME' = 'SLIME',
  'WFTM' = 'WFTM',
  'USDC' = 'USDC',
}

export interface FarmConfig {
  pid: number
  contractPid: number
  lpSymbol: string
  lpAddresses: string
  tokenSymbol: string
  tokenAddresses: string
  quoteTokenSymbol: QuoteToken
  quoteTokenAdresses: string
  multiplier?: string
  isTokenOnly?: boolean
  startTime?: number
  display?: string
  quorum?: number
  ignore?: boolean
  type: number
}

export interface PoolConfig {
  sousId: number
  tokenName: string
  stakingTokenName: QuoteToken
  stakingLimit?: number
  stakingTokenAddress?: string
  contractAddress: string
  tokenPerBlock: string
  harvest?: boolean
  isFinished?: boolean
  tokenDecimals: number
  startBlock?: number
  endBlock?: number
}
