import BigNumber from 'bignumber.js'
import erc20ABI from 'config/abi/erc20.json'
import masterchefABI from 'config/abi/masterchef.json'
import multicall from 'utils/multicall'
import farmsConfig from 'config/constants/farms'
import { getMasterChefAddress } from 'utils/addressHelpers'

export const fetchFarmUserAllowances = async (chainId: number, account: string) => {
  const calls = farmsConfig[chainId]
    .filter((x) => !x.ignore)
    .map((farm) => {
      const masterChefAdress = getMasterChefAddress(chainId, farm.type)
      return {
        address: farm.isTokenOnly ? farm.tokenAddresses : farm.lpAddresses,
        name: 'allowance',
        params: [account, masterChefAdress],
      }
    })

  const rawLpAllowances = await multicall(chainId, erc20ABI, calls)

  return rawLpAllowances.map((lpBalance) => new BigNumber(lpBalance).toJSON())
}

export const fetchFarmUserTokenBalances = async (chainId: number, account: string) => {
  const calls = farmsConfig[chainId]
    .filter((x) => !x.ignore)
    .map((farm) => {
      const lpContractAddress = farm.isTokenOnly ? farm.tokenAddresses : farm.lpAddresses
      return {
        address: lpContractAddress,
        name: 'balanceOf',
        params: [account],
      }
    })

  const rawTokenBalances = await multicall(chainId, erc20ABI, calls)
  return rawTokenBalances.map((tokenBalance) => new BigNumber(tokenBalance).toJSON())
}

export const fetchFarmUserStakedBalances = async (chainId: number, account: string) => {
  const calls = farmsConfig[chainId]
    .filter((x) => !x.ignore)
    .map((farm) => ({
      address: getMasterChefAddress(chainId, farm.type),
      name: 'userInfo',
      params: [farm.contractPid, account],
    }))

  const rawStakedBalances = await multicall(chainId, masterchefABI, calls)
  return rawStakedBalances.map((stakedBalance) => new BigNumber(stakedBalance[0]._hex).toJSON())
}

export const fetchFarmUserEarnings = async (chainId: number, account: string) => {
  const calls = farmsConfig[chainId]
    .filter((x) => !x.ignore)
    .map((farm) => ({
      address: getMasterChefAddress(chainId, farm.type),
      name: 'pendingReward',
      params: [farm.contractPid, account],
    }))

  const rawEarnings = await multicall(chainId, masterchefABI, calls)
  return rawEarnings.map((earnings) => new BigNumber(earnings).toJSON())
}
