import poolsConfig from 'config/constants/pools'
import sousChefABI from 'config/abi/sousChef.json'
import cakeABI from 'config/abi/cake.json'
import { QuoteToken } from 'config/constants/types'
import multicall from 'utils/multicall'
import BigNumber from 'bignumber.js'

export const fetchPoolsBlockLimits = async (chainId: number) => {
  const poolsWithEnd = poolsConfig.filter((p) => p.sousId !== 0)
  const callsStartBlock = poolsWithEnd.map((poolConfig) => {
    return {
      address: poolConfig.contractAddress,
      name: 'startBlock',
    }
  })
  const callsEndBlock = poolsWithEnd.map((poolConfig) => {
    return {
      address: poolConfig.contractAddress,
      name: 'bonusEndBlock',
    }
  })

  const starts = await multicall(chainId, sousChefABI, callsStartBlock)
  const ends = await multicall(chainId, sousChefABI, callsEndBlock)

  return poolsWithEnd.map((cakePoolConfig, index) => {
    const startBlock = starts[index]
    const endBlock = ends[index]
    return {
      sousId: cakePoolConfig.sousId,
      startBlock: new BigNumber(startBlock).toJSON(),
      endBlock: new BigNumber(endBlock).toJSON(),
    }
  })
}

export const fetchPoolsTotalStatking = async (chainId: number) => {
  const pools = poolsConfig.filter((p) => p.stakingTokenName !== QuoteToken.BNB)

  const callsNonBnbPools = pools.map((poolConfig) => ({
    address: poolConfig.stakingTokenAddress,
    name: 'balanceOf',
    params: [poolConfig.contractAddress],
  }))

  const staked = await multicall(chainId, cakeABI, callsNonBnbPools)

  return [
    ...pools.map((p, index) => ({
      sousId: p.sousId,
      totalStaked: new BigNumber(staked[index]).toJSON(),
    })),
  ]
}
