import BigNumber from 'bignumber.js'
import { ethers } from 'ethers'

export const approve = async (lpContract, masterChefContract, account, amount?: BigNumber) => {
  return lpContract.methods
    .approve(masterChefContract.options.address, amount || ethers.constants.MaxUint256)
    .send({ from: account })
}

export const swapTokens = async (contract, amount, account) => {
  return contract.methods
    .deposit(new BigNumber(amount).times(new BigNumber(10).pow(18)).toString())
    .send({ from: account })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

export const swapBackTokens = async (contract, amount, account) => {
  return contract.methods
    .withdraw(new BigNumber(amount).times(new BigNumber(10).pow(18)).toString())
    .send({ from: account })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

