import React, { useState } from 'react'

const CACHE_KEY = 'CHAINID'

const ChainContext = React.createContext({
  chainId: 56,
  toggleChainId: (x: number) => console.info(x),
})

const ChainContextProvider = ({ children }) => {
  const [chainId, setChainid] = useState<number>(() => {
    const x = localStorage.getItem(CACHE_KEY)
    return parseInt(x ? JSON.parse(x) : 56)
  })

  const toggleChainId = (x: number) => {
    setChainid(() => {
      localStorage.setItem(CACHE_KEY, JSON.stringify(x))
      return x
    })
  }

  return <ChainContext.Provider value={{ chainId, toggleChainId }}>{children}</ChainContext.Provider>
}

export { ChainContext, ChainContextProvider }
