import addresses from 'config/constants/contracts'

export const getMasterYieldAddress = () => addresses.masterYield[56]
export const getMulticallAddress = (chainId) => addresses.mulltiCall[parseInt(chainId)]
export const getYieldContract = () => addresses.lpYield[56]
export const getCTokenAddress = () => addresses['C100']
export const getTokenAddress = (chainId) => (parseInt(chainId) === 97 ? addresses.A009 : addresses.G009)
export const getYieldTokenAddress = () => addresses['A010']

export const getMasterChefAddress = (chainId, type) => {
  if (parseInt(chainId) === 97) return type === 0 ? addresses.masterChef[97] : addresses.masterChef10[97]
  return addresses.masterChef[parseInt(chainId)]
}
