import { useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { getCTokenAddress, getTokenAddress, getYieldTokenAddress } from 'utils/addressHelpers'
import erc20ABI from 'config/abi/erc20.json'
import multicall from 'utils/multicall'
import { useMasterchef } from './useContract'
import useRefresh from './useRefresh'

const useAgentInfo = (): any => {
  const [val, setVal] = useState()
  const { account, chainId } = useWallet()
  const { fastRefresh } = useRefresh()
  const masterChefContract = useMasterchef(56, 0)
  useEffect(() => {
    if (parseInt(chainId as any) !== 56) return
    if (account && !val) masterChefContract.methods.members(account).call().then(setVal)
  }, [account, chainId, val, masterChefContract, fastRefresh])
  return val
}

const zero = new BigNumber(0)

export const useSupplyState = () => {
  const { chainId } = useWallet()
  const [balance, setBalance] = useState([zero, zero, zero])
  const sdo = getTokenAddress(chainId)
  const sso = getYieldTokenAddress()
  const sbo = getCTokenAddress()

  useEffect(() => {
    const isBsc = parseInt(chainId as any) === 97
    const fetchTotals = async () => {
      const calls = [
        {
          address: sdo,
          name: 'totalSupply',
          params: [],
        },
      ]

      if (isBsc) {
        calls.push({
          address: sso,
          name: 'totalSupply',
          params: [],
        })
        calls.push({
          address: sbo,
          name: 'totalSupply',
          params: [],
        })
      }

      const data = await multicall(chainId, erc20ABI, calls)
      setBalance([
        new BigNumber(data[0]).div(new BigNumber(10).pow(18)),
        isBsc ? new BigNumber(data[1]).div(new BigNumber(10).pow(18)) : zero,
        isBsc ? new BigNumber(data[2]).div(new BigNumber(10).pow(18)) : zero,
      ])
    }

    fetchTotals()
  }, [sdo, sbo, sso, chainId])

  return balance
}

export default useAgentInfo
