import { useWallet } from '@binance-chain/bsc-use-wallet'
import useAgentInfo from 'hooks/useAgentInfo'
import React, { useMemo } from 'react'
import Button from '../../components/Button/Button'
import { useWalletModal } from '../WalletModal'
import { Login } from '../WalletModal/types'

interface Props {
  login: Login
  logout: () => void
}

const getLabel = (v: number) => {
  if (v === 250) return 'FANTOM'
  if (v === 137) return 'POLYGON'

  return 'BSC'
}

const UserBlock: React.FC<Props> = ({ login, logout }) => {
  const { account, chainId } = useWallet()
  const info = useAgentInfo()
  const label = useMemo(() => getLabel(parseInt(chainId.toString())), [chainId])
  const { onPresentConnectModal, onPresentAccountModal } = useWalletModal(label === 'BSC', login, logout, account, info?.invitationsLeft)
  const accountEllipsis = account ? `${account.substring(0, 4)}...${account.substring(account.length - 4)}` : null

  return (
    <div>
      <Button
        size="sm"
        onClick={() => {
          console.info(chainId)
        }}
      >
        {label}
      </Button>
      {account ? (
        <Button
          size="sm"
          variant="tertiary"
          onClick={() => {
            if (label === 'BSC') onPresentAccountModal()
          }}
        >
          {label === 'BSC' ? `${accountEllipsis} - Invitations: ${info?.invitationsLeft || 0}` : accountEllipsis}
        </Button>
      ) : (
        <Button
          size="sm"
          onClick={() => {
            onPresentConnectModal()
          }}
        >
          Connect
        </Button>
      )}
    </div>
  )
}

export default UserBlock
