import BigNumber from 'bignumber.js'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import useRefresh from 'hooks/useRefresh'
import { fetchPoolsPublicDataAsync, fetchPoolsUserDataAsync } from './actions'
import { State, Farm, Pool } from './types'
import { QuoteToken } from '../config/constants/types'
import { fetchFarmsPublicDataAsync } from './farms'

const ZERO = new BigNumber(0)

export const useFetchPublicData = () => {
  const dispatch = useDispatch()
  const { slowRefresh } = useRefresh()
  const { chainId } = useWallet()

  useEffect(() => {
    try {
      if (parseInt(chainId.toString()) === 137) return
      dispatch(fetchFarmsPublicDataAsync(chainId))

      if (parseInt(chainId.toString()) !== 56) return
      dispatch(fetchPoolsPublicDataAsync(chainId))
    } catch (error) {
      console.error(error)
    }
  }, [dispatch, slowRefresh, chainId])
}

export const useFarms = (): Farm[] => {
  const { chainId } = useWallet()
  const farms = useSelector((state: State) => state.farms.data[chainId])
  return farms
}

export const useFarmFromPid = (pid): Farm => {
  const { chainId } = useWallet()
  return useSelector((state: State) => state.farms.data[chainId].find((f) => f.pid === pid))
}

export const useFarmFromSymbol = (lpSymbol: string): Farm => {
  const { chainId } = useWallet()
  const farm = useSelector((state: State) => state.farms.data[chainId].find((f) => f.lpSymbol === lpSymbol))
  return farm
}

export const useFarmUser = (pid) => {
  const farm = useFarmFromPid(pid)
  return {
    allowance: farm.userData ? new BigNumber(farm.userData.allowance) : new BigNumber(0),
    tokenBalance: farm.userData ? new BigNumber(farm.userData.tokenBalance) : new BigNumber(0),
    stakedBalance: farm.userData ? new BigNumber(farm.userData.stakedBalance) : new BigNumber(0),
    earnings: farm.userData ? new BigNumber(farm.userData.earnings) : new BigNumber(0),
  }
}

// Pools

export const usePools = (): Pool[] => {
  const { chainId, account } = useWallet()
  const { fastRefresh } = useRefresh()
  const dispatch = useDispatch()
  useEffect(() => {
    if (account) {
      dispatch(fetchPoolsUserDataAsync(chainId, account))
    }
  }, [account, chainId, dispatch, fastRefresh])

  const pools = useSelector((state: State) => state.pools.data)
  return pools
}

export const usePoolFromPid = (sousId): Pool => {
  const pool = useSelector((state: State) => state.pools.data.find((p) => p.sousId === sousId))
  return pool
}

const usePriceFromPid = (pid: number): BigNumber => {
  const bnbPriceUSD = usePriceBnbBusd()
  const farm = useFarmFromPid(pid)
  return farm && farm.tokenPriceVsQuote ? bnbPriceUSD.times(farm.tokenPriceVsQuote) : ZERO
}

// Prices
export const usePriceBnbBusd = (): BigNumber => {
  const pid = 999
  const farm = useFarmFromPid(pid)
  return farm && farm.tokenPriceVsQuote ? new BigNumber(farm.tokenPriceVsQuote) : ZERO
}

export const usePriceCakeBusd = (): BigNumber => {
  const pid = 998
  return usePriceFromPid(pid)
}

export const usePriceYieldBusd = (): BigNumber => {
  const pid = 997
  return usePriceFromPid(pid)
}

export const usePriceDaoBusd = (): BigNumber => {
  const pid = 996
  return usePriceFromPid(pid)
}

export const usePriceABnb = (): BigNumber => {
  const bnbPriceUSD = usePriceBnbBusd()
  const { chainId } = useWallet()
  const pid = parseInt(chainId as any) === 250 ? 1 : 6
  const farm = useFarmFromPid(pid)
  return farm && farm.tokenPriceVsQuote ? bnbPriceUSD.times(farm.tokenPriceVsQuote) : ZERO
}

const calculateTotalTokenOnly = (
  farm: Farm,
  bnbPrice: BigNumber,
  aPrice: BigNumber,
  cakePrice: BigNumber,
): BigNumber => {
  const base = new BigNumber(farm.tokenAmount)

  if (farm.quoteTokenSymbol === QuoteToken.BNB) return base.times(bnbPrice)
  if (farm.quoteTokenSymbol === QuoteToken['A009']) return base.times(aPrice)
  if (farm.quoteTokenSymbol === QuoteToken.CAKE) return base.times(cakePrice)

  return base
}

export const calculateTotal = (farm: Farm, bnbPrice: BigNumber, aPrice: BigNumber, cakePrice: BigNumber): BigNumber => {
  if (farm.isTokenOnly && farm.tokenAmount) return calculateTotalTokenOnly(farm, bnbPrice, aPrice, cakePrice)

  const base = new BigNumber(farm.lpTotalInQuoteToken || 0)

  if (farm.quoteTokenSymbol === QuoteToken.BNB) return base.times(bnbPrice)
  if (farm.quoteTokenSymbol === QuoteToken['A009']) return base.times(aPrice)
  if (farm.quoteTokenSymbol === QuoteToken.CAKE) return base.times(cakePrice)

  return base
}

export const useTotalValue = (): BigNumber => {
  const farms = useFarms()
  const bnbPrice = usePriceBnbBusd()
  const aPrice = usePriceABnb()
  const cakePrice = usePriceCakeBusd()
  let value = new BigNumber(0)

  for (let i = 0; i < farms.length; i++) {
    const farm = farms[i]

    if (!farm.ignore && farm.lpTotalInQuoteToken) {
      const val = calculateTotal(farm, bnbPrice, aPrice, cakePrice)

      value = value.plus(val)
    }
  }

  return value
}
