import React, { useState } from 'react'
import Input from 'components/Input'
import { useMasterchef } from 'hooks/useContract'
import { invite } from 'utils/callHelpers'
import Button from '../../components/Button/Button'
import Text from '../../components/Text/Text'
import LinkExternal from '../../components/Link/LinkExternal'
import Flex from '../../components/Flex/Flex'
import { Modal } from '../Modal'
import CopyToClipboard from './CopyToClipboard'
import { localStorageKey } from './config'

interface Props {
  account: string
  logout: () => void
  onDismiss?: () => void
  invitationsLeft: string
  isBsc: boolean
}

const InvitationForm: React.FC<any> = ({ account }) => {
  const [val, setVal] = useState()
  const contract = useMasterchef(56, 0)
  const onChange = (e) => setVal(e.target.value)
  const onClick = async () => {
    const inviteAddress = val
    await invite(contract, account, inviteAddress)
    setVal(undefined)
  }

  return (
    <Input
      endAdornment={
        <Button size="sm" onClick={onClick} disabled={!val} placeholder="0x0000....">
          Send Invitation
        </Button>
      }
      onChange={onChange}
      placeholder="0"
      value={val}
    />
  )
}

const AccountModal: React.FC<Props> = ({ account, logout, invitationsLeft, isBsc, onDismiss = () => null }) => (
  <Modal title="Agent Information" onDismiss={onDismiss}>
    <Text
      fontSize="16px"
      bold
      style={{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis', marginBottom: '8px' }}
    >
      {account}
    </Text>
    <Flex mb="32px">
      <Text>Your invitations left:&nbsp;</Text>
      <Text>{invitationsLeft || 0}</Text>
    </Flex>
    {Number(invitationsLeft) > 0 && (
      <Flex mb="32px">
        <InvitationForm account={account} />
      </Flex>
    )}
    <Flex mb="32px">
      {isBsc ? (
        <LinkExternal small href={`https://bscscan.com/address/${account}`} mr="16px">
          View on BscScan
        </LinkExternal>
      ) : (
        <LinkExternal small href={`https://ftmscan.com/address/${account}`} mr="16px">
          View on FtmScan
        </LinkExternal>
      )}
      <CopyToClipboard toCopy={account}>Copy Address</CopyToClipboard>
    </Flex>
    <Flex justifyContent="center">
      <Button
        size="sm"
        variant="secondary"
        onClick={() => {
          logout()
          window.localStorage.removeItem(localStorageKey)
          onDismiss()
          window.location.reload()
        }}
      >
        Logout
      </Button>
    </Flex>
  </Modal>
)

export default AccountModal
